<template>
  <el-row class="form-bg">
    <el-col :span="10" :offset="6">
      <el-form ref="form" :rules="rules" :model="forminfo" label-width="100px">
        <el-form-item label="轮播图名称" prop="title">
          <el-input v-model="forminfo.title"></el-input>
        </el-form-item>
        <el-form-item label="轮播图">
          <el-upload
            action="#"
            :auto-upload="false"
            list-type="picture-card"
            :limit="1"
            :file-list="fileList"
            :on-change="handleChange"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove">
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </el-form-item>
        <el-form-item label="状态">
          <el-switch v-model="forminfo.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submit">立即{{$route.params.id ? '修改':'创建'}}</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
import { addBanner, getBannerList, editBanner } from "@/request/banner";
export default {
  data() {
    return {
      forminfo: {
        title: "",
        img: "",
        status: 1, // 状态，1是正常，2是禁用
      },
      dialogImageUrl: '',
      dialogVisible: false,
      fileList:[],  // [{name:"",url:""}]
      rules:{
          title: [{ required: true, message: '请输入轮播图名称', trigger: 'blur' }],
      }
    };
  },
  created() {
    // 如果是修改页面，就请求当前页面的数据！
    if (this.$route.params.id) {
      this.getinfo();
    }
  },
  methods: {
      handleChange(file, fileList){
        // 将原生的文件信息放入到img字段中
        this.forminfo.img = file.raw
      },
      handleRemove(file, fileList) {
         this.fileList = [];
         this.forminfo.img = ""
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
    async getinfo() {
      this.forminfo = await getBannerList(this.$route.params.id);
      if(this.forminfo.img){
        this.fileList = [{
          name:this.forminfo.catename,
          url:this.$host+this.forminfo.img
        }]
      }
    },
    submit() {
      // 验证
      // 如果文件信息和字段信息一起提交，那么需要使用FormData对象！
      let fd = new FormData();
      for (let key in this.forminfo) {
        fd.append(key,this.forminfo[key])
      }
      this.$refs.form.validate(valid=>{
        if(valid){
          // 提交数据
          if (this.$route.params.id) {
            editBanner(this.$route.params.id,fd);
          } else {
            addBanner(fd);
          }
        }else{
            this.$message.warning('验证失败')
        }
      })
    },
    reset(){
      if (this.$route.params.id) {
        this.getinfo();
      }else{
        this.$refs.form.resetFields();
      }
       
    }
  },
  components: {},
};

// BootStrap
</script>
<style scoped>
</style>


